const addShadow = () => {
	document.addEventListener("DOMContentLoaded", function () {
		const navbar = document.querySelector(".navbar")
		if (window.innerWidth < 992) {
			navbar.classList.add("navbar-shadow")
		}
		window.addEventListener("scroll", function () {
			if (window.innerWidth > 991) {
				if (window.scrollY > 150) {
					navbar.classList.add("navbar-shadow")
				} else {
					navbar.classList.remove("navbar-shadow")
				}
			}
		})
	})
}
addShadow()

const accordion = () => {
	const accordion = document.querySelector(".accordion")
	const accordionBtns = document.querySelectorAll(".accordion__box-btn")

	function openAccordionItems() {
		const isActive = this.nextElementSibling.classList.toggle("active")
		this.setAttribute("aria-expanded", isActive ? "true" : "false")
		closeAccordionItem(this)
	}

	const closeAccordionItem = (currentBtn) => {
		const allActiveItems = document.querySelectorAll(".accordion__info.active")
		allActiveItems.forEach((item) => {
			if (item.previousElementSibling !== currentBtn) {
				item.classList.remove("active")
				const btn = item.previousElementSibling
				if (btn) {
					btn.setAttribute("aria-expanded", "false")
				}
			}
		})
	}

	const clickOutsideAccordion = (e) => {
		if (!accordion.contains(e.target)) {
			closeAccordionItem()
		}
	}

	accordionBtns.forEach((btn) =>
		btn.addEventListener("click", openAccordionItems)
	)

	window.addEventListener("click", clickOutsideAccordion)
}
accordion()

const animations = () => {
	const animateItems = document.querySelectorAll(".animate-item")
	function getRootMargin() {
		const isMobile = window.innerWidth < 768
		return isMobile ? "0.001" : ".5"
	}
	const options = {
		root: null,
		threshold: getRootMargin(),
	}

	const observer = new IntersectionObserver((entries, observer) => {
		entries.forEach((entry) => {
			if (entry.isIntersecting) {
				entry.target.classList.add("animate")
				observer.unobserve(entry.target)
			}
		})
	}, options)

	animateItems.forEach((section) => {
		observer.observe(section)
	})
}
animations()

const burgerMenu = () => {
	const burger = document.querySelector(".burger")
	const toggleMenu = () => {
		const menu = document.querySelector(".navbar__items")
		const body = document.querySelector("body")
		menu.classList.toggle("menu-active")
		body.classList.toggle("no-scroll")
	}

	const navbarItems = document.querySelectorAll(".navbar__items-item")
	navbarItems.forEach((link) => {
		link.addEventListener("click", function () {
			const menu = document.querySelector(".navbar__items")
			if (menu.classList.contains("menu-active")) {
				menu.classList.remove("menu-active")
				document.querySelector("body").classList.remove("no-scroll")
			}
		})
	})

	burger.addEventListener("click", toggleMenu)
}
burgerMenu()

const arrowToTop = () => {
	document.addEventListener("DOMContentLoaded", function () {
		const scrollToTopBtn = document.querySelector(".arrow-to-top")

		window.addEventListener("scroll", function () {
			if (window.scrollY > 300) {
				scrollToTopBtn.classList.add("arrow-to-top-active")
			} else {
				scrollToTopBtn.classList.remove("arrow-to-top-active")
			}
		})
	})
}
arrowToTop()
// footer year auto change
const footerYear = () => {
	const footerNumber = document.querySelector("#footer-number")
	let date = new Date()
	let footerYear = date.getFullYear()
	footerNumber.textContent = footerYear
}
footerYear()